*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  font-family: "Playfair Display", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  font-style: normal;
  margin: 0;
  padding-left: 0;
  padding-right: 0;
  width: 100%;
  color: #000000;
  background-color: #eeeae6; }

a {
  text-decoration: none;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 30px;
    margin: 0 auto; } }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
    padding: 0 35px;
    width: auto; } }

@media (max-width: 767px) {
  .container {
    max-width: 767px;
    padding: 0 15px; } }

.background-wrapper {
  background-image: url("../img/peter-larsen.jpg");
  background-color: #eeeae6;
  background-repeat: no-repeat;
  height: 1024px;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .background-wrapper {
      background-image: url("../img/peter-larsen@2x.jpg");
      background-repeat: no-repeat; } }

.main-nav {
  width: 1600px;
  margin: 0 auto;
  min-height: 200px; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.main-nav__item {
  padding-right: 50px;
  margin-top: 80px; }
  .main-nav__item:last-child {
    margin-left: auto; }

.main-nav__item-link {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%; }
  .main-nav__item-link--favorites {
    color: #1d1c1d; }

.promo {
  position: relative;
  width: 1700px;
  margin: 0 auto;
  padding-left: 200px;
  padding-right: 150px; }

.promo__header {
  position: relative;
  font-style: normal;
  font-weight: 700;
  font-size: 144px;
  line-height: 120%;
  text-transform: uppercase;
  margin-bottom: 0;
  padding-left: 145px;
  padding-right: 50px; }
  .promo__header::after {
    position: absolute;
    content: "";
    top: 42px;
    left: 430px;
    background-color: #000000;
    background-position: center;
    border-radius: 50%;
    width: 21px;
    height: 21px; }

.promo__header-italic {
  font-style: italic; }

.promo__header-normal {
  font-style: normal; }

.promo__text-company {
  font-style: normal;
  font-weight: 700;
  font-size: 64px;
  line-height: 120%;
  margin: 0;
  padding-left: 715px;
  text-transform: uppercase; }
  .promo__text-company--right {
    padding-left: 910px; }

.promo__text {
  position: absolute;
  top: 20px;
  right: 130px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 120%;
  padding-left: 30px;
  padding-right: 30px;
  width: 300px; }

.promo__button {
  position: absolute;
  top: 54px;
  right: 500px;
  width: 100px;
  background-color: transparent;
  border: none; }
  .promo__button::after {
    position: absolute;
    content: "";
    top: -30px;
    left: 0;
    background-position: center;
    border-radius: 50%;
    border: 1px solid black;
    width: 100px;
    height: 100px; }

.preview-granite__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }

.preview-granite__text {
  position: absolute;
  top: 85px;
  left: -20px;
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 120%;
  color: #eeeae6;
  writing-mode: vertical-lr; }

.preview-granite__item {
  position: relative;
  padding: 10px; }
  .preview-granite__item:nth-child(1) img {
    width: 168px;
    height: 295px; }
  .preview-granite__item:nth-child(2) img {
    width: 358px;
    height: 358px; }
  .preview-granite__item:nth-child(3) img {
    width: 252px;
    height: 252px; }
  .preview-granite__item:nth-child(4) img {
    width: 252px;
    height: 421px; }
  .preview-granite__item:nth-child(5) img {
    width: 252px;
    height: 251px; }
  .preview-granite__item:nth-child(6) img {
    width: 281px;
    height: 493px; }
  .preview-granite__item:nth-child(7) img {
    width: 110px;
    height: 252px; }
  .preview-granite__item:nth-child(8) img {
    width: 160px;
    height: 255px; }
